.gpt3__blog-title h1 {
  font-family: var(--font-family);
  font-size: 62px;
  font-weight: 800;
  line-height: 75px;
  letter-spacing: -0.04em;
  width: 50%;
}

.gpt3__blog-title {
  width: 100%;
  text-align: left;

  margin-bottom: 5rem;
}

.gpt3__blog {
  display: flex;
  flex-direction: column;
}

.gpt3__blog-container {
  display: flex;
  flex-direction: row;
}

.gpt3__blog-group-A {
  flex: 0.75;
  display: flex;
  margin-right: 2rem;
}
.gpt3__blog-group-B {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
}

@media screen and (max-width: 700px) {
  .gpt3__blog-container_group-A {
    grid-template-columns: repeat(1, 1fr);
  }

  .gpt3__blog-container_group-A .gpt3__blog-container {
    width: 100%;
  }

  .gpt3__blog-title h1 {
    font-size: 46px;
    line-height: 52px;
  }
}

@media screen and (max-width: 550px) {
  .gpt3__blog-title h1 {
    font-size: 34px;
    line-height: 42px;
  }
}
